<script>
  import * as animateScroll from "svelte-scrollto";

  export let src;
  export let alt;
  export let innerText;
  export let extraClass = "";
  export let textSize = "text-2xl md:text-2.5xl";
  export let scrollTo = undefined;
  export let onClick = undefined;

  const handleClick = () => {
    if (scrollTo) animateScroll.scrollTo({ element: scrollTo });
    if (onClick) onClick();
  };
</script>

<main>
  <button
    class={`flex items-center  px-6 py-2 
      bg-fiery-rose rounded-full hover:bg-opacity-75 transition duration-75 ease-in ${extraClass}`}
    type="button"
    on:click={handleClick}>
    <img {src} {alt} />
    <span class={`font-body font-bold ${textSize} ml-2 `}>{innerText}</span>
  </button>
</main>
