<script>
  // Actions
  import { scrollto } from "svelte-scrollto";
  import { sendEmail, validateEmail } from "./utils/email";

  // Components
  import Button from "./components/Button.svelte";
  import Award from "./components/vectors/Award.svelte";
  import List from "./components/vectors/List.svelte";
  import Message from "./components/vectors/Message.svelte";

  // Images/Vectors
  let imgs = {
    logo: "images/logo.svg",
    ellipse_1: "images/ellipse_1.svg",
    avatar: "images/avatar.svg",
    message: "images/feather_message-circle.svg",
    arrow_down: "images/arrow-down.svg",
    decoration: "images/decoration.svg",
    glyde_frame: "images/glyde_frame.png",
    simresolvemos_frame: "images/simresolvemos_frame.png",
    clients: "images/clients.svg",
    footer_bg: "images/footer_bg.png",
    github: "images/github.svg",
    gmail: "images/gmail.svg",
    linkedin: "images/linkedin.svg",
  };

  // Variables
  let services = [
    {
      name: "Desktop",
      icon: "images/feather_monitor.svg",
      description: "Programas nativos",
    },
    {
      name: "Mobile",
      icon: "images/feather_smartphone.svg",
      description: "Apps iOS/Android",
    },
    {
      name: "Web",
      icon: "images/feather_globe.svg",
      description: "Sites, ecommerce...",
    },
    {
      name: "Design",
      icon: "images/feather_pen-tool.svg",
      description: "Protótipos de UI/UX",
    },
  ];

  let service_steps = [
    {
      name: "Contato",
      description:
        "Juntos, discutimos sobre o seu projeto, e pensamos no melhor plano de ação.",
    },
    {
      name: "Orçamento",
      description:
        "Você escolhe a melhor proposta para você e para o seu negócio.",
    },
    {
      name: "Desenvolvimento",
      description:
        "Seus planos começam a tomar forma, e você será constantemente atualizado sobre o andamento do produto.",
    },
  ];

  let navLinks = [
    { name: "Serviços", icon: List, selector: "#services" },
    { name: "Portfolio", icon: Award, selector: "#projects" },
    { name: "Contato", icon: Message, selector: "#contact" },
  ];

  // Form
  let formError = false;
  let formSuccess = false;

  let from_name = "";
  let from_email = "";
  let from_message = "";

  const handleFormSubmit = () => {
    formSuccess = false;

    if (!from_name || !from_email || !from_message) {
      formError = "Por favor preencha todos os campos.";
      return;
    }
    if (!validateEmail(from_email)) {
      formError = "Por favor digite um e-mail valido.";
      return;
    }

    formError = false;

    sendEmail(from_name, from_email, from_message);

    formSuccess = true;
  };
</script>

